<template>
    <div class="Alterpha">
        <el-select v-model="value" filterable remote placeholder="" :remote-method="remoteMethod" :loading="loading">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
        <el-button type="primary" icon="el-icon-search"  @click="search(value)"></el-button>
        <!--  -->
        <div class="Alterpha_content" >
            <div class="sym" v-for="(item, index) in phalist" :key="index" :data="phalist">
            <el-popover placement="top-start" :title=phalist[index].name width="200" trigger="hover" :content=phalist[index].remark>
             <el-button slot="reference" @click="handleEdit(index, phalist[index])">{{phalist[index].name}}</el-button >
            </el-popover>
            </div>
        </div>
        <!--  -->
        <el-dialog class="Sres" title="" :model="Alterpha" :visible.sync="dialogalterVisible" width="30%" >
            <el-input v-model="Alterpha.name"  placeholder=""></el-input>
            <el-input v-model="Alterpha.remark" placeholder=""></el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delPha" type="danger"> </el-button>
                <el-button  @click="updatePha" type="primary"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      dialogalterVisible: false, //
      options: [],
      value: [],
      loading: false,
      list: []
    };
  },
  created() {
    this.getphalist();
  },
  computed: {
    ...mapGetters(["phalist"]), //
    ...mapGetters(["Alterpha"]) //
  },
  mounted() {
    this.list = this.phalist.map(item => {
      // console.log(item.name);
      return {
        value: item.name,
        label: item.name
      };
    });
  },
  methods: {
    getphalist() {
      //
      this.$store.dispatch("getPhaList");
    },
    handleEdit(index, row) {
      //   console.log(index, row);
      this.Alterpha.id = row.Id;
      this.Alterpha.name = row.name;
      this.Alterpha.remark = row.remark;
      this.dialogalterVisible = true;
    },
    delPha() {
      //
      this.$store
        .dispatch("delPha", this.Alterpha.id)
        .then(res => {
          this.dialogalterVisible = false;
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              this.$message.success("!");
            })
            .catch(() => {
              this.$message.info("");
            });
        })
        .catch(err => {
          console.log(err);
        });
    },
    updatePha() {
      //
      if (this.Alterpha.name == "") {
        this.$message.error("");
      } else {
        this.$store
          .dispatch("updatePha", this.Alterpha)
          .then(res => {
            this.$message.success("");
            this.dialogalterVisible = false;
          })
          .catch(error => {
            console.log(error);
            this.$message.error("");
          });
      }
    },
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter(item => {
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options = [];
      }
    },
    search(value) {
      this.dialogalterVisible = true;
      this.Alterpha.name = value;
      // console.log(value);
    }
  }
};
</script>

<style lang="less" scoped>
.Alter_title {
  // background-color: lawngreen;
  width: 100%;
  .el-input {
    margin-top: 10px;
    width: 35%;
  }
}
.Alterpha_content {
  width: 95%;
  margin: 0 auto;
  padding-top: 30px;
  // background-color: pink;
  .el-button {
    width: 150px;
  }
  .sym {
    float: left;
    margin: 10px 10px;
  }
}
.Sres {
  .el-input {
    margin: 10px 0px;
  }
}
</style>



