<template>
    <div class="Manger">
       
       
        <div class="Manger_content">
          
        <el-button  @click="dialogcreateVisible = true" class="add" type="primary" plain></el-button>
        <el-table :data="tableData" style="width: 100%">
                <el-table-column label="" width="300">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.date }}</span>
                </template>
                </el-table-column>
                <el-table-column label="" width="300">
                <template slot-scope="scope">
                    <div slot="reference" class="name-wrapper">
                        <el-tag size="medium">{{ scope.row.name }}</el-tag>
                    </div>
                </template>
                </el-table-column>
                <el-table-column label="" width="300">
                <template slot-scope="scope">
                    <div slot="reference" class="name-wrapper">
                        <el-tag size="medium">{{ scope.row.role }}</el-tag>
                    </div>
                </template>
                </el-table-column>
                <el-table-column label="">
                <template slot-scope="scope">
                    <el-button size="mini"  @click="handleEdit(scope.$index, scope.row)" class="el-icon-edit-outline"></el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)" class="el-icon-delete"></el-button>
                </template>
                </el-table-column>
            </el-table>
        </div>
        <!--  -->
        <el-dialog class="Mres" title="" :visible.sync="dialogcreateVisible" width="30%" >
            <el-input v-model="manger.name"  placeholder=""></el-input>
            <el-input type="password" v-model="manger.passwd" placeholder=""></el-input>
            <el-select v-model="manger.role" clearable placeholder="">
                <el-option v-for="item in mangerType" :key="item.label" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogcreateVisible = false"> </el-button>
                <el-button  @click="addAdmin" type="primary"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
         <el-dialog class="Mres" title="" :visible.sync="dialogupdateVisible" width="30%" >
            <el-input v-model="updatemanger.name"  placeholder=""></el-input>
            <el-input type="password" v-model="updatemanger.passwd" placeholder=""></el-input>
            <el-input type="password" v-model="updatemanger.password" placeholder=""></el-input>
            <el-select v-model="updatemanger.role" clearable placeholder="">
                <el-option v-for="item in mangerType" :key="item.label" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogupdateVisible = false"> </el-button>
                <el-button  @click="updateAdmin" type="primary"> </el-button>
            </span>
        </el-dialog>


    </div>
</template>
<script>
import { mapGetters } from "vuex";

import time from "../../tools/time.js";
import crypto from "../../tools/crypto.js";
export default {
  data() {
    return {
      date: "",
      dialogcreateVisible: false, //
      dialogupdateVisible: false, //
      mangerType: [
        //
        { value: "General", label: "" },
        { value: "Super", label: "" }
      ]
      // manger: [
      //   //
      //   {
      //     date: "",
      //     name: "",
      //     passwd: "",
      //     password: "",
      //     role: []
      //   }
      // ],
      // tableData: [
      //   //
      //   {
      //     date: "",
      //     name: "",
      //     role: ""
      //   }
      // ],
      // updatemanger: [
      //   //
      //   {
      //     id: "",
      //     name: "",
      //     passwd: "",
      //     role: ""
      //   }
      // ]
    };
  },
  created() {
    this.getAdmin();
  },
  computed: {
    ...mapGetters(["manger"]), //
    ...mapGetters(["tableData"]), //
    ...mapGetters(["updatemanger"]) //
  },
  mounted() {
    this.date = time();
  },

  methods: {
    // 
    handleEdit(index, row) {
      // console.log(index, row);
      this.updatemanger.id = row.Id;
      this.updatemanger.name = row.name;
      this.updatemanger.passwd = row.passwd;
      this.updatemanger.role = row.role;
      this.dialogupdateVisible = true;
    },
    updateAdmin() {
      this.updatemanger.passwd = this.crypto(this.updatemanger.passwd);
      this.updatemanger.password = this.crypto(this.updatemanger.password);
      if (this.updatemanger.passwd == this.updatemanger.password) {
        this.axios
          .post("/api/admin/updateAdmin", {
            id: this.updatemanger.id,
            name: this.updatemanger.name,
            passwd: this.updatemanger.passwd,
            role: this.updatemanger.role
          })
          .then(res => {
            if (res.status == 200) {
              this.dialogupdateVisible = false;
              this.$message({
                showClose: true,
                message: ",",
                type: "success"
              });
            } else {
              this.$message.error("");
            }
            // console.log(res);
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      }
    },
    // 
    handleDelete(index, row) {
      // console.log(index, row);
      var id = row.Id;
      this.$confirm(
        "" + row.name + ", ?",
        "",
        { type: "warning" }
      )
        .then(() => {
          var resdel = this.axios
            .post("/api/admin/delAdmin", {
              id: row.Id
            })
            .then(res => {
              this.$message.success("" + row.name + "!");
            });
        })
        .catch(err => {
          console.log(err);
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    getAdmin() {
      this.$store.dispatch("getAdmin");
    },
    //
    addAdmin() {
      this.$store.dispatch("addAdmin", this.manger).then(res => {
        if (res.status == 200) {
          this.$message.success(",");
        } else {
          this.$message.error("");
        }
        this.dialogcreateVisible = false;
      });
    }
  }
};
</script>
<style lang="less" scoped>
.Manger {
  .Manger_content {
    width: 90%;
    margin: 0 auto;
    text-align: left;
    // border-top: 1px solid #000;
    // background-color: lightcoral;
    .add {
      float: right;
      margin-bottom: 10px;
      font-size: 20px;
    }
  }
  .Mres {
    .el-input {
      margin-bottom: 10px;
    }
    .el-select {
      width: 100%;
    }
  }
}
</style>


