<template>
<div class="Altercon">
  <div class="Altercon_title">
    
  </div>
  <div class="Altercon_content">
    <el-table :data="Conlist" style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label=" ID">
              <span>{{ props.row.uid }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.date }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sym }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.diag }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.pres }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.remark }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="" prop="uid"></el-table-column>
      <el-table-column label="" prop="sym"></el-table-column>
      <el-table-column label="" prop="diag"></el-table-column>
      <el-table-column label="">
        <template slot-scope="props">
          <el-button size="mini" @click="handleEdit(props.$index, props.row)" class="el-icon-edit-outline"></el-button>
          <el-button size="mini" type="danger" @click="handleDelete(props.$index, props.row)" class="el-icon-delete"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog class="Mres" title="" :visible.sync="dialogupdateVisible" width="30%" >
      <el-form label-position="left" label-width="80px">
        <el-form-item label="">
          <el-input v-model="updateCon.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="updateCon.date"></el-input>
        </el-form-item>
        <el-form-item  label="">
          <el-input type="textarea" v-model="updateCon.sym"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="updateCon.diag"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="updateCon.pres"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="updateCon.remark"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
          <el-button @click="dialogupdateVisible = false"> </el-button>
          <el-button  @click="updateConsilia" type="primary"> </el-button>
      </span>
    </el-dialog>
  </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      dialogupdateVisible: false
      // namedatesymdiagpresremark
    };
  },
  created() {
    this.getConsilia();
  },
  computed: {
    ...mapGetters(["Conlist"]), //
    ...mapGetters(["updateCon"]) //
  },
  methods: {
    //
    handleEdit(index, row) {
      // console.log(index, row);
      this.dialogupdateVisible = true;
      this.updateCon.id = row.Id;
      this.updateCon.name = row.name;
      this.updateCon.date = row.date;
      this.updateCon.sym = row.sym;
      this.updateCon.pres = row.pres;
      this.updateCon.diag = row.diag;
      this.updateCon.remark = row.remark;
    },
    updateConsilia() {
      this.$store
        .dispatch("updateConsilia", this.updateCon)
        .then(res => {
          this.dialogupdateVisible = false;
          this.$message.success(",");
          // console.log(res);
        })
        .catch(error => {
          console.log(error);
          this.$message.error("");
        });
    },
    //
    handleDelete(index, row) {
      // console.log(index, row);
      var id = row.Id;
      this.$confirm(", ?", "", {
        type: "warning"
      })
        .then(() => {
          var resdel = this.$store.dispatch("delCon", row.Id).then(res => {
            this.$message.success("");
          });
        })
        .catch(err => {
          console.log(err);
          this.$message.info("");
        });
    },
    //
    getConsilia() {
      this.$store
        .dispatch("getConsilia")
        .then(res => {})
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
<style lang="less" scoped>
.Altercon_title {
  margin-bottom: 20px;
}

.Altercon_content {
  width: 80%;
  text-align: left;
  margin: 0 auto;
}
</style>









