import Vue from 'vue'
import Vuex from 'vuex'
import login from './modules/login'
import manger from './modules/manger'
import routenav from './modules/routenav'
import pharmacy from './modules/pharmacy'
import symptom from './modules/symptom'
import consilia from './modules/consilia'
import record from './modules/record'
import sick from './modules/sick'
import getters from './getters'


Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    token: sessionStorage.getItem('token'), //
    name: sessionStorage.getItem('name'), //
    role: sessionStorage.getItem('role'), //
    manger: [], //
    tableData: [], //
    updatemanger: [], //
    newrouter: [], //
    routenav: [], //
    phalist: [], //
    Alterpha: [], //
    symlist: [], //
    altersym: [], //
    Conlist: [], //
    updateCon: [], //
    everycealist: [], //
    mycrealist: [], //
    updateCreation: [], //
    personallist: [], //
    sicklists: [], //
    userSicklist: [], //
  },
  modules: {
    login, // 
    manger, //
    routenav, //
    pharmacy, //
    symptom, //
    consilia, //
    record, //
    sick, //
  },
  getters,

});
export default store;



