import Admin from "@/admin/Admin.vue";
import Sheet from "@/admin/Sheet.vue";
import Manger from "@/admin/Manger.vue";
import Record from "@/admin/Record.vue";
import Personal from "@/admin/Personal.vue";
import Symptom from "@/admin/symptom/Symptom.vue";
import Altersym from "@/admin/symptom/Altersym.vue";
import Pharmacy from "@/admin/pharmacy/Pharmacy.vue";
import Alterpha from "@/admin/pharmacy/Alterpha.vue";
import Consilia from "@/admin/consilia/Consilia.vue";
import Altercon from "@/admin/consilia/Altercon.vue";
import Person from "@/admin/personal/Person.vue";
import Release from "@/admin/personal/Release.vue";
import Patient from "@/admin/personal/Patient.vue";
import Composition from "@/admin/personal/Composition.vue";

import Content from "@/admin/Content.vue"; //  
import ss from "@/admin/1.vue"; //

import {
  resolve
} from "path";

const admin = [{
  //
  path: "/admin",
  name: "Admin",
  component: Admin
}];

export default admin;
export const powerRouter = [{
  //
  //
  path: "/",
  redirect: "/sheet",
  name: "sheet",
  component: Sheet,
  hidden: false,
  children: [{
      path: "/sheet",
      name: "",
      icon: "el-icon-tickets"
      // component: Sheet,
    },
    {
      path: "/manger",
      name: "",
      component: Manger,
      icon: "el-icon-setting",
      meta: {
        role: "Super"
      }
    },
    { //
      path: "/personal",
      name: "",
      icon: "el-icon-service",
      component: Content,
      children: [{
          path: "/record",
          name: "",
          component: Record,
        },
        {
          path: "/personal",
          name: "",
          component: Personal,

          children: [{
              path: "/personal/person",
              name: "",
              component: Person
            },
            {
              path: "/personal/release",
              name: "",
              component: Release
            },
            {
              path: "/personal/patient",
              name: "",
              component: Patient
            },
            {
              path: "/personal/composition",
              name: "",
              component: Composition
            }
          ]
        },
      ]
    },
    {
      path: "/sheet1",
      name: "",
      icon: "el-icon-edit",
      component: Content,

      meta: {
        role: "General"
      },
      children: [{
          //
          path: "/symptom",
          name: "",
          component: Symptom
        },
        {
          //
          path: "/altersym",
          name: "",
          component: Altersym
        }
      ]
    },
    {
      path: "/sheet2",
      name: "",
      icon: "el-icon-edit",
      component: Content,
      meta: {
        role: "General"
      },
      children: [{
          //
          path: "/pharmacy",
          name: "",
          component: Pharmacy
        },
        {
          //
          path: "/alterpha",
          name: "",
          component: Alterpha
        }
      ]
    },
    {
      path: "/sheet3",
      name: "",
      icon: "el-icon-view",
      component: Content,
      children: [{
          //
          path: "/consilia",
          name: "",
          component: Consilia,
          meta: {
            role: "General"
          }
        },
        {
          //
          path: "/altercon",
          name: "",
          component: Altercon,
          meta: {
            role: "General"
          }
        }
      ]
    },

  ]
}];



