<template>
<!--  -->
    <div class="Altersym">
        <el-select v-model="value" filterable remote placeholder="" :remote-method="remoteMethod" :loading="loading">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
        <el-button type="primary" icon="el-icon-search"  @click="search(value)"></el-button>
        <!--  -->
        <div class="Altersym_content" >
            <div class="sym" v-for="(item, index) in symlist" :key="index" :data="symlist">
            <el-popover placement="top-start" :title=symlist[index].name width="200" trigger="hover" :content=symlist[index].remark>
             <el-button slot="reference" @click="handleEdit(index, symlist[index])">{{symlist[index].name}}</el-button >
            </el-popover>
            </div>
        </div>
        <!--  -->
        <el-dialog class="Sres" title="" :model="altersym" :visible.sync="dialogalterVisible" width="30%" >
            <el-input v-model="altersym.name"  placeholder=""></el-input>
            <el-input v-model="altersym.remark" placeholder=""></el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delSym" type="danger"> </el-button>
                <el-button  @click="updateSym" type="primary"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      dialogalterVisible: false, //
      options: [],
      value: [],
      loading: false,
      list: []
    };
  },
  created() {
    this.getSymlist();
  },
  computed: {
    ...mapGetters(["symlist"]), //
    ...mapGetters(["altersym"]) //
  },
  mounted() {
    this.list = this.symlist.map(item => {
      // console.log(item.name);
      return {
        value: item.name,
        label: item.name
      };
    });
  },
  methods: {
    getSymlist() {
      //
      this.$store
        .dispatch("getSymlist")
        .then(res => {})
        .catch(err => {
          this.$message.error("");
          console.log(err);
        });
    },
    handleEdit(index, row) {
      //   console.log(index, row);
      this.altersym.id = row.Id;
      this.altersym.name = row.name;
      this.altersym.remark = row.remark;
      this.dialogalterVisible = true;
    },
    delSym() {
      //
      this.$store
        .dispatch("delSym", this.altersym.id)
        .then(res => {
          this.dialogalterVisible = false;
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(() => {
              this.$message.success("!");
            })
            .catch(() => {
              this.$message.info("");
            });
        })
        .catch(error => {
          this.$message.error("");
          console.log(error);
        });
    },
    updateSym() {
      //
      if (this.altersym.name == "") {
        this.$message.error("");
      } else {
        this.$store
          .dispatch("updateSym", this.altersym)
          .then(res => {
            // console.log(res);

            this.dialogalterVisible = false;
            this.$message.success("");
          })
          .catch(err => {
            this.$message.error("");
            console.log(err);
          });
      }
    },
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter(item => {
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options = [];
      }
    },
    search(value) {
      this.dialogalterVisible = true;
      this.altersym.name = value;
      // console.log(value);
    }
  }
};
</script>

<style lang="less" scoped>
.Altersym_content {
  width: 95%;
  padding-top: 30px;
  margin: 0 auto;
  .el-button {
    width: 150px;
  }
  .sym {
    float: left;
    margin: 10px 10px;
  }
}
.Sres {
  .el-input {
    margin: 10px 0px;
  }
}
</style>



